body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Add,
.Save {
  position: absolute;
  padding-top: 0px;
  width: 100%;

  margin: 0 auto;
}
.Add input {
  line-height: 35px;
  width: 89%;
  border-radius: 16px;
  border: 2px solid #ededed;
  font-size: 20px;
}
.search button {
  background: url(/TodoList/static/media/search.c985d74f.png) no-repeat;
  height: 40px;
  width: 40px;
  margin: 4px 14px;
  font-size: 0px;
  border: none;
  background-size: cover;
}

.search {
  float: right;
  width: 40%;
  margin: 30px 8px;
}

.search input {
  border-radius: 16px;
  border: 2px solid #ededed;
  line-height: 28px;
  padding: 0 8px;
  width: 80%;
}

.Add button,
.Save button {
  line-height: 35px;
  width: 100px;
  border-radius: 16px;
  background-color: #0095ff;
  color: white;
  border: none;
  font-size: 20px;
  margin: 4px 14px;
}

.Tabs {
  position: relative;
  display: inline-block;
  width: 100%;
  margin: 56px auto 10px;
}

.tablinks {
  /* display: inline-block; */
  padding: 1em;
  background: #ededed;
  color: black;
  cursor: pointer;
  width: 30.833333%;
  float: left;
}

.tablinks:hover {
  opacity: 0.7;
}

.tablinks.active {
  background: #0095ff;
  color: rgb(255, 255, 255);
}

@media only screen and (max-width: 1200px) {
  .tablinks {
    width: 30.033333%;
  }
}

@media only screen and (max-width: 969px) {
  .tablinks {
    width: 28%;
  }
}

@media only screen and (max-width: 599px) {
  .tablinks {
    width: 25%;
  }
}

@media only screen and (max-width: 376px) {
  .tablinks {
    width: 23%;
  }
}

.delete {
  background: url(/TodoList/static/media/delete.4d1ffe40.png) no-repeat;
  height: 40px;
  width: 40px;
  background-size: cover;
  border: none;
  margin: 0 4px;
  border-radius: 50%;
}

.edit {
  background: url(/TodoList/static/media/edit.a6239530.png) no-repeat;
  background-size: cover;
  height: 40px;
  width: 40px;
  margin: 0 4px;
  border-radius: 50%;
  border: none;
}

.task {
  text-decoration: none;
  width: 100%;
  margin: 8px auto;
  line-height: 45px;
  border-radius: 10px;
  background: #f0eded;
}

.tasks.complete {
  text-decoration: line-through;
}

.tasks {
  padding: 0 0px;
  width: 65%;
  float: left;
}

.buttons {
  width: 25%;
  margin: 10px 0;
  float: right;
}

.clearfix {
  content: "";
  overflow: hidden;
}
.task input {
  float: left;
  padding: 0 8px;
  margin: 12px 0;
  line-height: 40px;
  width: 60%;
  font-size: 16px;
}

.Complete {
  background: url(/TodoList/static/media/complete.38bef3c8.png) no-repeat;
  height: 40px;
  width: 40px;
  background-size: cover;
  border: none;
  margin: 0 4px;
  border-radius: 50%;
}
.Undo {
  background: url(data:image/png;base64,) no-repeat;
  height: 40px;
  width: 40px;
  background-size: cover;
  border: none;
  margin: 0 4px;
  border-radius: 50%;
}

.App {
  text-align: center;
  position: relative;
  margin: 0 auto;
  max-width: 1290px;
}

.header h1 {
  color: #0095ff;
  font-size: 50px;
  float: left;
}

.clearfix {
  content: "";
  overflow: hidden;
}

